import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

// import { products } from '@/data/products';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,

  },

  mutations: {
    // addPoductToCart(state, { productId, amount }) {
    //   const item = state.cartProducts.find(item => item.productId === productId);
    //   if (item) {
    //     item.amount += amount
    //   } else {
    //     state.cartProducts.push({
    //       productId: productId,
    //       amount: amount
    //     })
    //   }
    // },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },

    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },

    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(item => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },

    // deleteCartProduct(state, productId) {
    //   // вариант как в видео удаляет весь массив, по одмому элементу так:
    //   let arr = state.cartProducts;
    //   arr = arr.filter((el) => el.productId !== productId);
    //   state.cartProducts = arr;
    // },

    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },

    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },

    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        }
      })
    },

  },

  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product
        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
            //product: products.find(p => p.id === item.productId)//
          },
        }
      })
    },

    cartTotalPrice(state, getters) {
      let result = 0;
      if (getters.cartDetailProducts.length > 0) {
        result = getters.cartDetailProducts.reduce((accum, item) => (item.product.price * item.amount) + accum, 0);
      }
      return result;
    },

  },

  //  свойства можно менять только мутацией, но она действует только синхронно,
  //  асинхронно менять через действия.
  actions: {
    loadOrderInfo(context, orderId) {
      return axios
        .get(API_BASE_URL + "/api/orders/" + orderId, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateOrderInfo', response.data);
        })
    },

    loadCart(context) {
      return (new Promise(resolve => setTimeout(resolve, 300)))
        .then(() => {
          return (axios
            .get(API_BASE_URL + "/api/baskets", {
              params: {
                userAccessKey: context.state.userAccessKey
              }
            })
            .then(respons => {
              if (!context.state.userAccessKey) {
                localStorage.setItem('userAccessKey', respons.data.user.accessKey);
                context.commit("updateUserAccessKey", respons.data.user.accessKey);

              }
              context.commit("updateCartProductsData", respons.data.items);
              context.commit("syncCartProducts");
            })
          )
        })
    },

    addProductToCart(context, { productId, amount }) {
      return (new Promise(resolve => setTimeout(resolve, 700)))
        .then(() => {
          return axios
            .post(API_BASE_URL + '/api/baskets/products',
              { productId: productId, quantity: amount },
              {
                params: {
                  userAccessKey: context.state.userAccessKey,
                }
              })
            .then(respons => {
              context.commit("updateCartProductsData", respons.data.items);
              context.commit("syncCartProducts");
            })
        })
    },

    updateCartProductAmount(context, { productId, amount }) {
      this.commit("updateCartProductAmount", { productId, amount });
      if (amount < 1) { return; }
      return (new Promise(resolve => setTimeout(resolve, 700)))
        .then(() => {
          return axios
            .put(API_BASE_URL + '/api/baskets/products',
              { productId: productId, quantity: amount },
              {
                params: {
                  userAccessKey: context.state.userAccessKey,
                }
              })
            .then(respons => {
              context.commit("updateCartProductsData", respons.data.items);
            })
            .catch(() => {
              context.commit("syncCartProducts");
            })
        })
    },

    deleteCartProduct(context, productId) {
      return (
        axios.delete(API_BASE_URL + "/api/baskets/products", {
          data: { productId: productId },
          params: {
            userAccessKey: context.state.userAccessKey
          }
        },
        )
          .then((respons) => {
            context.commit("updateCartProductsData", respons.data.items);
            context.commit("syncCartProducts");
          })
      )
    },

  }

});
