<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> {{ countProducts }} товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color.sync="filterColor"
      />
      <section class="catalog">
        <PreloadShow v-if="productsLoading" />

        <FailedShow
          v-else-if="productLoadingFailed"
          :fieledShow="fieledShow"
          @tryAgain="loadProducts"
        />

        <ProductList :products="products" v-else-if="productsData" />
        <!-- @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)" -->

        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import { API_BASE_URL } from "@/config.js";
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from "axios";
import showPreloadMixin from "@/mixins/showPreloaderMixin.js";
import showFailedMixin from "@/mixins/showFailedMixin.js";

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
  },

  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: "0",

      page: 1,
      productsPerPage: 6,

      productsData: null,
    };
  },

  mixins: [showPreloadMixin, showFailedMixin],

  computed: {
    // filteredProducts() {
    //   let filteredProducts = this.products;
    //   if (this.filterPriceFrom > 0) {
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.price >= this.filterPriceFrom
    //     );
    //   }
    //   if (this.filterPriceTo > 0) {
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.price <= this.filterPriceTo
    //     );
    //   }
    //   if (this.filterCategoryId > 0) {
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.categoryId === this.filterCategoryId
    //     );
    //   }
    //   if (this.filterColor != "0") {
    //     filteredProducts = filteredProducts.filter(
    //       (product) =>
    //         product.properties.property === "color" &&
    //         product.properties.values.includes(this.filterColor)
    //     );
    //   }
    //   return filteredProducts;
    // },

    products() {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return {
              ...product,
              image: product.image.file.url,
            };
          })
        : [];
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    },

    countProducts() {
      //return this.filteredProducts.length;
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productLoadingFailed = false;
      this.failedStatus = "";
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        return axios
          .get(API_BASE_URL + "/api/products", {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              // colorId: this.filterColor,
            },
          })
          .then((respons) => (this.productsData = respons.data))
          .catch((respons) => {
            this.productLoadingFailed = true;
            this.fieledShow.showButton = true;
            if (respons) {
              this.fieledShow.failedStatus =
                "Ошибка загрузки товаров. Повторите позже...";
              this.fieledShow.responsText =
                respons.message + ". " + respons.code;
            } else {
              this.fieledShow.failedStatus = "Нет ответа от сервера.";
              this.fieledShow.responsText = "";
            }
          })
          .then(() => (this.productsLoading = false));
      }, 500);
    },
  },

  watch: {
    page() {
      this.loadProducts();
    },

    filterCategoryId() {
      this.loadProducts();
    },

    filterPriceFrom() {
      this.loadProducts();
    },

    filterPriceTo() {
      this.loadProducts();
    },

    filterColor() {
      this.loadProducts();
    },
  },

  created() {
    this.loadProducts();
  },
};
</script>
