<template>
  <div>
    <HeaderApp />
    <!-- <MainPage v-if="currentPage === 'main'" />
  <ProductPage v-else-if="currentPage === 'product'" />
  <NotPageFound v-else /> -->
    <!-- передача события без шины событий
