  /> -->
    <!-- это без роутера <component :is="currentPageComponent" :page-params="currentPageParams" /> -->

    <PreloadShow v-if="productsLoading" />

    <FailedShow
      v-else-if="productLoadingFailed"
      :fieledShow="fieledShow"
      @tryAgain="loadCart"
    />

    <router-view />

    <FooterApp />
  </div>
</template>

 <script>
// import CartIndicator from "@/components/CartIndicator.vue";
import HeaderApp from "@/components/HeaderApp.vue";
import FooterApp from "@/components/FooterApp.vue";
import { mapActions } from "vuex";
import { mapMutations } from "vuex";
import PreloadShow from "@/components/PreloadShow.vue";
import FailedShow from "@/components/FailedShow.vue";

export default {
  components: { HeaderApp, FooterApp, FailedShow, PreloadShow },

  data() {
    return {
      productsLoading: false,
      productLoadingFailed: false,
      fieledShow: {
        failedStatus: "",
        responsText: "",
        showButton: true,
      },
    };
  },

  created() {
    this.productLoadingFailed = false;
    this.productsLoading = true;

    const userAccessKey = localStorage.getItem("userAccessKey");
    if (userAccessKey) {
      this.updateUserAccessKey(userAccessKey);
    }

    // this.$store.dispatch("loadCart");
    this.loadCart()
      .then(() => {
        this.productLoadingFailed = false;
        this.productsLoading = false;
      })
      .catch((error) => {
        this.productLoadingFailed = true;
        this.productsLoading = false;
        this.fieledShow.failedStatus = "Ошибка загрузки корзины.";
        this.fieledShow.responsText = error.message + ". " + error.code;
      });
  },
  methods: {
    ...mapActions(["loadCart"]),
    ...mapMutations(["updateUserAccessKey"]),
  },
};

