<template>
  <div>
    <PreloadShow v-if="productLoading" />

    <FailedShow
      v-else-if="productLoadingFailed"
      :fieledShow="fieledShow"
      @tryAgain="deleteProduct(item.productId)"
    />

    <li class="cart__item product">
      <div class="product__pic">
        <img
          :src="item.product.image"
          width="120"
          height="120"
          :alt="item.product.title"
        />
      </div>

      <h3 class="product__title">
        {{ item.product.title }}
      </h3>

      <!-- <p class="product__info">Объем: <span>128GB</span></p> -->
      <span class="product__code"> Артикул: {{ item.productId }} </span>

      <div class="product__counter form__counter">
        <!-- <button type="button" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button> -->

        <AmountMinus :width="10" :height="10" @amountMinus="amountMinus" />

        <input type="text" v-model.number="amount" name="count" />

        <AmountPlus :width="10" :height="10" @amountPlus="amountPlus" />

        <!-- <button type="button" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button> -->
      </div>

      <b class="product__price">
        {{ (item.product.price * item.amount) | numberFormat }} ₽
      </b>

      <button
        class="product__del button-del"
        type="button"
        aria-label="Удалить товар из корзины"
        @click.prevent="deleteProduct(item.productId)"
      >
        <svg width="20" height="20" fill="currentColor">
          <use xlink:href="#icon-close"></use>
        </svg>
      </button>
    </li>
  </div>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapActions } from "vuex";
import AmountMinus from "@/components/AmountMinus.vue";
import AmountPlus from "@/components/AmountPlus.vue";
import showPreloadMixin from "@/mixins/showPreloaderMixin.js";
import showFailedMixin from "@/mixins/showFailedMixin.js";

export default {
  components: { AmountMinus, AmountPlus },

  props: ["item"],

  mixins: [showPreloadMixin, showFailedMixin],

  filters: { numberFormat },

  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        //вызов мутации обновить кол-во товара в корзине
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },

  methods: {
    amountPlus() {
      this.amount += 1;
    },

    amountMinus() {
      if (this.amount > 1) {
        this.amount -= 1;
      }
    },

    ...mapActions(["deleteCartProduct"]),

    deleteProduct(productId) {
      this.productLoading = true;
      this.productLoadingFailed = false;
      this.fieledShow.showButton = true;

      this.deleteCartProduct(productId)
        .then(() => {
          this.productLoading = false;
          this.productLoadingFailed = false;
        })
        .catch((respons) => {
          this.productLoading = false;
          this.productLoadingFailed = true;
          if (respons) {
            this.fieledShow.failedStatus =
              "Произошла ошибка при удалении товара из корзины.";
            this.fieledShow.responsText = respons.message + ", " + respons.code;
          } else {
            this.fieledShow.failedStatus =
              "Нет ответа от сервера при удалении товара из корзины.";
            this.fieledShow.responsText = "";
          }
        });
    },

    // ...mapMutations({ deleteProduct: "deleteCartProduct" }),
    // проксирование мутации вместо метода deleteProduct
    // deleteProduct(productId) {
    //   //вызов мутации удалить товар из корзины
    //   this.$store.commit("deleteCartProduct", productId);
    // },
  },
};
</script>
